import React, { FunctionComponent, useState } from 'react'
import cls from 'classnames'
import { Props } from './index.d'
import './style.scss'

const IntrinsicImage: FunctionComponent<Props> = (props: Props) => {
  const { preSrc, src } = props

  const [isLoaded, setIsLoaded] = useState(false)

  const onLoaded = (e: any) => {
    setIsLoaded(prev => !prev)
  }
  return (
    <div className='IntrinsicImage__wrapper'>
      <div className='IntrinsicImage__inner'>
        <img onLoad={onLoaded} className='IntrinsicImage__main' src={src} />
        <img
          className={cls('IntrinsicImage__pre', {
            'IntrinsicImage__pre--loaded': isLoaded,
          })}
          src={preSrc}
        />
      </div>
    </div>
  )
}

export default IntrinsicImage
